:root {
  --bg-dark: #f82121;
  --bg-light: #fa2d59cc;
  --btn_dark: #fff;
  --btn_light: #f82121;
}

.loginPage__container input {
  font-family: "Poppins", sans-serif;
}

.loginPage__container {
  position: relative;
  width: 100%;
  background-color: #fff;
  min-height: 100vh;
  overflow: hidden;
}

.loginPage__container .forms-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.loginPage__container .signin-signup {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  left: 75%;
  width: 50%;
  transition: 1s 0.7s ease-in-out;
  display: grid;
  grid-template-columns: 1fr;
  z-index: 5;
}

.loginPage__container form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0rem 5rem;
  transition: all 0.2s 0.7s;
  overflow: hidden;
  grid-column: 1 / 2;
  grid-row: 1 / 2;
}

.loginPage__container form.sign-up-form {
  opacity: 0;
  z-index: 1;
}

.loginPage__container form.sign-in-form {
  z-index: 2;
}

.loginPage__container .title {
  font-size: 2.2rem;
  color: #444;
  margin-bottom: 10px;
}

.loginPage__container .input-field {
  max-width: 380px;
  width: 100%;
  background-color: #f0f0f0;
  margin: 10px 0;
  height: 55px;
  border-radius: 55px;
  display: grid;
  grid-template-columns: 15% 85%;
  padding: 0 0.4rem;
  position: relative;
}

.loginPage__container .input-field i {
  text-align: center;
  line-height: 55px;
  color: #acacac;
  transition: 0.5s;
  font-size: 1.1rem;
}

.loginPage__container .input-field input {
  background: none;
  outline: none;
  border: none;
  line-height: 1;
  font-weight: 600;
  font-size: 1.1rem;
  color: #333;
}

.loginPage__container .input-field input::-webkit-input-placeholder {
  color: #aaa;
  font-weight: 500;
}

.loginPage__container .input-field input:-ms-input-placeholder {
  color: #aaa;
  font-weight: 500;
}

.loginPage__container .input-field input::-ms-input-placeholder {
  color: #aaa;
  font-weight: 500;
}

.loginPage__container .input-field input::placeholder {
  color: #aaa;
  font-weight: 500;
}

