.loginPage__container .btn {
  width: 150px;
  background-color: #f82121;
  background-color: var(--btn_light);
  border: 1px solid #fff;
  border: 1px solid var(--btn_dark);
  outline: none;
  height: 49px;
  border-radius: 49px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 600;
  margin: 10px 0;
  cursor: pointer;
  transition: 0.5s;
}

.loginPage__container .btn:hover {
  background-color: #fff;
  background-color: var(--btn_dark);
  color: black;
  border: 1px solid #f82121;
  border: 1px solid var(--btn_light);
}
.loginPage__container .panels-container {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

 .loginPage__container:before {
  content: "";
  position: absolute;
  height: 2000px;
  width: 2000px;
  top: -10%;
  right: 48%;
  transform: translateY(-50%);
  background-image: linear-gradient(-45deg, #f82121 0%, #fa2d59cc 100%);
  background-image: linear-gradient(-45deg, var(--bg-dark) 0%, var(--bg-light) 100%);
  transition: 1.8s ease-in-out;
  border-radius: 50%;
  z-index: 6;
}

.loginPage__container .image {
  width: 100%;
  transition: transform 1.1s ease-in-out;
  transition-delay: 0.4s;
}

.loginPage__container .panel {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-around;
  text-align: center;
  z-index: 6;
}

.loginPage__container .left-panel {
  pointer-events: all;
  padding: 3rem 17% 2rem 12%;
}

.loginPage__container .right-panel {
  pointer-events: none;
  padding: 3rem 12% 2rem 17%;
}

.loginPage__container .panel .content {
  color: #fff;
  transition: transform 0.9s ease-in-out;
  transition-delay: 0.6s;
}

.loginPage__container .panel h3 {
  font-weight: 600;
  line-height: 1;
  font-size: 1.5rem;
}

.loginPage__container .panel p {
  font-size: 0.95rem;
  padding: 0.7rem 0;
}

.loginPage__container .btn.transparent {
  margin: 0;
  background: none;
  border: 2px solid #fff;
  width: 130px;
  height: 41px;
  font-weight: 600;
  font-size: 0.8rem;
}
.loginPage__container .btn.transparent:hover {
  color: #fff;
}

.loginPage__container .right-panel .image,
.loginPage__container .right-panel .content {
  transform: translateX(800px);
}

/* ANIMATION */

.loginPage__container.sign-up-mode:before {
  transform: translate(100%, -50%);
  right: 52%;
}

.loginPage__container.sign-up-mode .left-panel .image,
.loginPage__container.sign-up-mode .left-panel .content {
  transform: translateX(-800px);
}

.loginPage__container.sign-up-mode .signin-signup {
  left: 25%;
}

.loginPage__container.sign-up-mode form.sign-up-form {
  opacity: 1;
  z-index: 2;
}

.loginPage__container.sign-up-mode form.sign-in-form {
  opacity: 0;
  z-index: 1;
}

.loginPage__container.sign-up-mode .right-panel .image,
.loginPage__container.sign-up-mode .right-panel .content {
  transform: translateX(0%);
}

.loginPage__container.sign-up-mode .left-panel {
  pointer-events: none;
}

.loginPage__container.sign-up-mode .right-panel {
  pointer-events: all;
  grid-column-start: 2;
}

@media (max-width: 870px) {
  .loginPage__container {
    min-height: 800px;
    height: 100vh;
  }
  .loginPage__container .signin-signup {
    width: 100%;
    top: 95%;
    transform: translate(-50%, -100%);
    transition: 1s 0.8s ease-in-out;
  }

  .loginPage__container .signin-signup,
  .loginPage__container.sign-up-mode .signin-signup {
    left: 50%;
  }

  .loginPage__container .panels-container {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 2fr 1fr;
  }

  .loginPage__container .panel {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 2.5rem 8%;
    grid-column: 1 / 2;
  }

  .loginPage__container .right-panel {
    grid-row: 3 / 4;
  }

  .loginPage__container .left-panel {
    grid-row: 1 / 2;
  }

  .loginPage__container .image {
    width: 200px;
    transition: transform 0.9s ease-in-out;
    transition-delay: 0.6s;
  }

  .loginPage__container .panel .content {
    padding-right: 15%;
    transition: transform 0.9s ease-in-out;
    transition-delay: 0.8s;
  }

  .loginPage__container .panel h3 {
    font-size: 1.2rem;
  }

  .loginPage__container .panel p {
    font-size: 0.7rem;
    padding: 0.5rem 0;
  }

  .loginPage__container .btn.transparent {
    width: 110px;
    height: 35px;
    font-size: 0.7rem;
  }

  .loginPage__container:before {
    width: 1500px;
    height: 1500px;
    transform: translateX(-50%);
    left: 30%;
    bottom: 68%;
    right: auto;
    right: initial;
    top: auto;
    top: initial;
    transition: 2s ease-in-out;
  }

  .loginPage__container.sign-up-mode:before {
    transform: translate(-50%, 100%);
    bottom: 32%;
    right: auto;
    right: initial;
  }

  .loginPage__container.sign-up-mode .left-panel .image,
  .loginPage__container.sign-up-mode .left-panel .content {
    transform: translateY(-300px);
  }

  .loginPage__container.sign-up-mode .right-panel .image,
  .loginPage__container.sign-up-mode .right-panel .content {
    transform: translateY(0px);
  }

  .loginPage__container .right-panel .image,
  .loginPage__container .right-panel .content {
    transform: translateY(300px);
  }

  .loginPage__container.sign-up-mode .signin-signup {
    top: 5%;
    transform: translate(-50%, 0);
  }
}

@media (max-width: 570px) {
  .loginPage__container form {
    padding: 0 1.5rem;
  }

  .loginPage__container .image {
    display: none;
  }
  .loginPage__container .panel .content {
    padding: 0.5rem 1rem;
  }
  .loginPage__container {
    padding: 1.5rem;
  }

  .loginPage__container:before {
    bottom: 72%;
    left: 50%;
  }

  .loginPage__container.sign-up-mode:before {
    bottom: 28%;
    left: 50%;
  }
}


*{
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
.bg__error{
  height: 100vh;
  background: -webkit-repeating-linear-gradient(-45deg, #71b7e6, #69a6ce, #b98acc, #ee8176, #b98acc, #69a6ce, #9b59b6);
  background-size: 400%;
}
#error-page{
  position: absolute;
  top: 10%;
  left: 15%;
  right: 15%;
  bottom: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  box-shadow: 0px 5px 10px rgba(0,0,0,0.1);
}
#error-page .content{
  max-width: 600px;
  text-align: center;
}
.content h2.header{
  font-size: 18vw;
  line-height: 1em;
  position: relative;
}
.content h2.header:after{
  position: absolute;
  content: attr(data-text);
  top: 0;
  left: 0;
  right: 0;
  background: -webkit-repeating-linear-gradient(-45deg, #71b7e6, #69a6ce, #b98acc, #ee8176, #b98acc, #69a6ce, #9b59b6);
  background-size: 100%;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 1px 1px 2px rgba(255,255,255,0.25);
  /* animation: animate 10s ease-in-out infinite; */
}
@keyframes animate {
  0%{
    background-position: 0 0;
  }
  25%{
    background-position: 100% 0;
  }
  50%{
    background-position: 100% 100%;
  }
  75%{
    background-position: 0% 100%;
  }
  100%{
    background-position: 0% 0%;
  }
}
.content h4{
  font-size: 1.5em;
  margin-bottom: 20px;
  text-transform: uppercase;
  color: #000;
  font-size: 2em;
  max-width: 600px;
  position: relative;
}
.content h4:after{
  position: absolute;
  content: attr(data-text);
  top: 0;
  left: 0;
  right: 0;
  text-shadow: 1px 1px 2px rgba(255,255,255,0.4);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.content p{
  font-size: 1.2em;
  color: #0d0d0d;
}
.content .btns{
  margin: 25px 0;
  display: inline-flex;
}
.content .btns a{
  display: inline-block;
  margin: 0 10px;
  text-decoration: none;
  border: 2px solid #69a6ce;
  color: #69a6ce;
  font-weight: 500;
  padding: 10px 25px;
  border-radius: 25px;
  text-transform: uppercase;
  transition: all 0.3s ease;
}
.content .btns a:hover{
  background: #69a6ce;
  color: #fff;
}

